
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);
  font-family: 'Inter', sans-serif;
  color: #fff;
}

.login-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.login-box {
  width: 100%;
  max-width: 400px;
  background: rgba(255,255,255,0.08);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 32px 24px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.4);
  text-align: center;
  transition: transform 0.3s;
}
.login-box:hover {
  transform: translateY(-5px);
}

.title {
  font-family: 'Poppins', sans-serif;
  font-size: 2.25rem;
  font-weight: 600;
  color: #77aaff;
  margin-bottom: 24px;
}

.subtitle {
  font-size: 1rem;
  margin-bottom: 16px;
}

.wallet {
  display: block;
  font-family: 'Courier New', monospace;
  background: rgba(255,255,255,0.1);
  padding: 8px 12px;
  border-radius: 8px;
  word-break: break-all;
  margin: 0 auto 24px;
  max-width: 100%;
}

.button {
  width: 100%;
  padding: 14px 0;
  margin: 8px 0;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, transform 0.1s;
}

.button:active {
  transform: scale(0.98);
}

/* Estilos por tipo de botón */
.button.connect {
  background: #0066ff;
  color: #fff;
}
.button.connect:hover {
  background: #0051cc;
}

.button.continue {
  background: #28a745;
  color: #fff;
}
.button.continue:hover {
  background: #1e7e34;
}

.button.disconnect {
  background: #ff4d4f;
  color: #fff;
}
.button.disconnect:hover {
  background: #cc0000;
}

.error {
  color: #ff6b6b;
  margin-bottom: 16px;
  font-size: 0.9rem;
}

/* RESPONSIVE */
@media (max-width: 500px) {
  .login-box {
    padding: 24px 16px;
  }
  .title {
    font-size: 1.8rem;
  }
  .button {
    font-size: 0.95rem;
    padding: 12px 0;
  }
}
