@font-face {
  font-family: 'PermanentMarker'; /* Nombre que usarás en CSS */
  src: url('fonts/PermanentMarker-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Daimon'; /* Nombre que usarás en CSS */
  src: url('fonts/Oswald-Regular.ttf') format('truetype');
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* App.css */
h1 {
  font-weight: bold; /* Negrita */
  color: #2f00ff; /* Verde estilo bosque */
  text-transform: uppercase; /* Mayúsculas */
  text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5); /* Sombra para resaltar */
  letter-spacing: 3px; /* Espaciado entre letras */
  text-align: center; /* Centrado */
  margin-top: 20px; /* Espaciado superior */
  font-family: 'PermanentMarker', sans-serif; /* Fuente moderna (puedes cambiarla) */
}
@keyframes glowing-body {
  0% {
    box-shadow: inset 0 0 10px rgba(0, 123, 255, 0.5),
                inset 0 0 20px rgba(0, 123, 255, 0.5);
  }
  50% {
    box-shadow: inset 0 0 20px rgba(0, 123, 255, 0.7),
                inset 0 0 40px rgba(0, 123, 255, 0.7);
  }
  100% {
    box-shadow: inset 0 0 10px rgba(0, 123, 255, 0.5),
                inset 0 0 20px rgba(0, 123, 255, 0.5);
  }
}

body {
  background-color: black; /* Fondo oscuro para resaltar el efecto */
  margin: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: glowing-body 2s infinite alternate;
}
